$mainBackgroundColor: #d41443;
$mainBackgroundColorMiddle: #e17992;
$mainBackgroundColorLight: #ffb8ca;
$maincolor: #ffffff;
//main styling
.header {
  background-color: $mainBackgroundColorLight;
  height: 502px;
  width: 100%;
  position: relative;
  overflow: hidden;
}
.logo-icecream {
  position: absolute;
  left: 20%;
}

.nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 6px 20px 0 20px;
  &__logo {
    width: 199px;
    height: 50px;

    z-index: 3;
  }
  &__hero {
    z-index: 4;
    position: relative;
    overflow: hidden;
    padding: 0 20px;
  }
  &__hero-down {
    position: relative;
    display: flex;
  }

  &__buttons {
    display: flex;
    z-index: 3;
    top: 0;
    margin-top: 20px;
  }
  &__motto {
    top: 0;
    width: 218px;
    font-family: 'Titan One';
    font-weight: 400;
    font-size: 26px;
    line-height: 30px;
    letter-spacing: 0.04em;
    color: $maincolor;
    margin: 0;
    margin-top: 137px;

    p {
      padding: 0;
      margin: 0;
      z-index: 4;
    }
    &--main {
      text-transform: uppercase;
    }
    &--accent {
      font-size: 48px;
      line-height: 46px;
      color: $mainBackgroundColor;
      text-transform: uppercase;
    }
  }
  &__background {
    position: absolute;
    z-index: 1;
    margin-top: 92px;
    margin-left: 83px;
    width: 171px;
    height: 410px;
    &--elipse {
      position: absolute;
      width: 346px;
      height: 344px;
      z-index: 0;
      margin-top: 72px;
    }
    &--tasty {
      display: none;
    }
    &--bio-eco-milk {
      display: none;
    }
  }
  &__milk {
    display: none;
  }
  &__numbers {
    display: none;
  }
  &__menu {
    display: none;
  }
}
//buttons
.buttons {
  z-index: 50;
  &__products {
    background: $mainBackgroundColor;
    border-radius: 20px;
    border: none;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 8px;
    line-height: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 61.2px;
    height: 24px;
    margin-right: 8px;
    color: $maincolor;
    text-decoration: none;
  }
  &__about {
    background: $maincolor;
    border-radius: 20px;
    border: none;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 8px;
    line-height: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 82.8px;
    height: 24px;
    text-decoration: none;
    color: $mainBackgroundColor;
  }
}
//mobile-menu//
.menu-toggle {
  height: 20px;
  width: 20px;
  display: flex;
  justify-content: center;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  outline: none;
}

.menu-container {
  position: fixed;
  top: 0;
  right: 0;
  width: 250px;
  height: 100vh;
  background-color: $mainBackgroundColorMiddle;
  z-index: 999;

  transform: translateX(100%);
  transition: transform 250ms ease-in-out;
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-container .menu-toggle {
  position: absolute;
  right: 20px;
  color: $maincolor;
}

.mobile-menu {
  padding: 0;
  margin-top: 60px;
  &__list {
    list-style: none;
    padding: 0;
    margin-left: 21px;
  }
  &__link {
    display: block;
    padding-top: 22px;
    color: $maincolor;
    text-decoration: none;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.28;
  }
  &__button {
    width: 209px;
    height: 44px;
    color: $mainBackgroundColor;
    background-color: $maincolor;
    border: none;
    border-radius: 22px;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.31;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-left: 21px;
    cursor: pointer;
  }
  a {
    text-decoration: none;
  }
}
.button__tick {
  margin-left: 16px;
}

.mobile-menu__link:hover {
  color: $mainBackgroundColor;
}
.open-button {
  position: relative;
  color: $mainBackgroundColor;
  stroke: 1px;
  margin-top: 18px;
  z-index: 50;
}
.close-button {
  color: $maincolor;
  margin-top: 23px;
}
.tablet__menu-button {
  display: none;
}
//order form modal
.modal__container {
  position: fixed;

  top: 0;
  left: 0;

  z-index: 999;

  &.in {
    .modal__background {
      background: rgba(0, 0, 0, 0);
      animation: fadeIn 0.5s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;

      .modal {
        animation: slideIn 0.5s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
      }
    }
  }

  &.out {
    animation: quickScaleDown 0s 0.5s linear forwards;

    .modal__background {
      animation: fadeOut 0.5s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;

      .modal {
        animation: slideOut 0.5s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
      }
    }
  }
}
.modal__background {
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  z-index: 47;
  background: rgba(0, 0, 0, 0.3);
}

.modal {
  position: absolute;
  top: 10%;
  overflow: auto;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  background-color: $maincolor;
  border-radius: 4px;

  button[type='button'] {
    position: absolute;
    top: 8px;
    right: 8px;
    width: 30px;
    height: 30px;
    background-color: $maincolor;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 50px;
    cursor: pointer;
  }
}

@keyframes fadeIn {
  0% {
    background: rgba(0, 0, 0, 0);
  }

  100% {
    background: rgba(0, 0, 0, 0.7);
  }
}

@keyframes fadeOut {
  0% {
    background: rgba(0, 0, 0, 0.7);
  }

  100% {
    background: rgba(0, 0, 0, 0);
  }
}

@keyframes quickScaleDown {
  0% {
    transform: scale(0);
  }

  99.9% {
    transform: scale(1);
  }

  100% {
    transform: scale(0);
  }
}

@keyframes slideIn {
  0% {
    transform: translateX(-1500px) scaleX(1) rotate(360deg);
  }

  70% {
    transform: translateX(30px) scaleX(0.9);
  }

  100% {
    transform: translateX(0px) scaleX(1) rotate(0deg);
  }
}

@keyframes slideOut {
  0% {
    transform: translateX(0px) scaleX(1) rotate(0deg);
  }

  30% {
    transform: translateX(-30px) scaleX(1.1);
  }

  100% {
    transform: translateX(1500px) scaleX(1) rotate(360deg);
  }
}

.is-hidden-order {
  display: none;
}
.order {
  padding-left: 30px;
  padding-right: 30px;
  background-image: url(/images/about/about-desctopsectionbg.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom;
  
  &__logo {
    width: 200px;
    padding: 20px 0 20px 0;
  }

  &__label > .label__title {
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.28;
    letter-spacing: 0.04em;
    color: $mainBackgroundColor;
  }

  &__container {
    display: flex;
    flex-direction: column;
  }

  &__input {
    width: 100%;
    height: 40px;
    text-indent: 10px;
    border: 1px solid rgba(33, 33, 33, 0.2);
    border-radius: 5px;
    margin-top: 4px;
    margin-bottom: 15px;
    outline: none;
  }

  &__button {
    width: 200px;
    height: 50px;
    background-color: $mainBackgroundColor;
    font-family: 'DM Sans';
    cursor: pointer;
    font-weight: 700;
    font-size: 16px;
    color: $maincolor;
    text-align: center;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    border-radius: 20px;
    border: none;
    margin: 15px auto 40px auto;
  }
}
#order__comment {
  width: 100%;
  height: 120px;

  resize: none;
}
