//media order modal
@media screen and (min-width: 479px) {
  .modal {
    width: 80%;
    min-width: calc(320px - 60px);
    max-width: 450px;
    max-height: 80%;
  }
}
@media screen and (min-width: 480px) and (max-width: 1199px) {
  .modal {
    width: 450px;
  }
}
@media screen and (min-width: 1200px) {
  .modal {
    width: 528px;
  }
}
